import React from 'react'

function Welcome2() {
    return (
        <section>
            <div className="block color-scheme-1">
                
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-10 col-lg-9">
                            <div className="section-sub-title center">
                                <article className="section-title-body white">
                                    <h1 className="head-title">
                                        <span>Rico Cunningham,</span> a creator of ideas and <span>applications</span> that will help bridge the <span>Knowledge Gap</span> in web3 technology.</h1>
                                    <p className="head-text">
                                    Welcome to my world of music and finance! As a DJ and music producer, I've always believed in pushing boundaries and breaking free from the status quo. That's why I'm excited about the future of blockchain technology and decentralized finance. With the power of these revolutionary tools, we can create a fairer and more transparent financial system that benefits everyone. Join me on my journey to explore this exciting new world, where music and finance come together in perfect harmony. Let's make a difference together!
                                    </p>
                                </article>
                            </div>   
                        </div>
                    </div>
                </div>

            </div>
        </section>
    )
}

export default Welcome2
