import React from 'react'
const services = [
    {
        _id:'001',
        classIcon:'ion-ios-analytics',
        title:'Technical Consulting',
        text:'As a technical consultant, I provide expertise and support to clients on complex technical projects with strong communication and project management skills, delivering solutions on time.'
    },
    {
        _id:'002',
        classIcon:'ion-ios-people',
        title:'Social Influence',
        text:'As a social influencer, I use my platform and expertise to provide guidance and support to brands and businesses, delivering impactful campaigns with strong communication and marketing skills.'
    },
    {
        _id:'003',
        classIcon:'ion-ios-play',
        title:'Music Production',
        text:'As a music producer, I leverage my skills and expertise to collaborate with artists and deliver high-quality productions, optimizing the creative process.'
    },
    {
        _id:'004',
        classIcon:'ion-network',
        title:'Network Marketing',
        text:'As a network marketer, I utilize my knowledge and experience to build relationships and guide individuals in achieving their business goals.'
    },
    {
        _id:'005',
        classIcon:'ion-ios-book',
        title:'Mentor and Trainer',
        text:'As a mentor and trainer, I utilize my knowledge and expertise to guide and support individuals in achieving their personal and professional goals, delivering impactful training sessions.'
    },
    {
        _id:'006',
        classIcon:'ion-laptop',
        title:'Digital Asset Design',
        text:'As a digital asset creator, I use my skills and expertise to design and develop high-quality digital assets, delivering innovative solutions and optimizing the production process.'
    }
]
function Service1() {
    return (
        <>
          <section  id="services"  className="bg-non-fixed">
            <div className="bg-inner-dark2"></div>
            <div className="bg-50-l"></div>
            
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-6 col-sm-12 col-xs-12 order-md-1 justify-content-center d-flex align-items-center">
                        <div className="pad-l-45 pad-r-45">
                            <div className="section-block-title">
                                <div className="section-title">   
                                    <article className="section-title-body mobile-mb-0">
                                        <h1 className="head-title wow fadeInDown animated" data-wow-duration="1.5s">Services</h1>
                                        <div className="section-col wow fadeInUp animated" data-wow-duration="1.5s"></div>
                                        <p className="head-text wow fadeInUp animated" data-wow-duration="1.5s">
                                            I am a multi talented individual servicing a community
                                        </p>
                                        <div className="h25"></div>
                                        <div className="fucts-block wow fadeInUp animated" data-wow-duration="1.5s">
                                            <div className="fuct">
                                                <h2>5000+</h2>
                                                <h4>Subscribers</h4>
                                            </div>
                                            <div className="fuct">
                                                <h2>1500+</h2>
                                                <h4>Followers</h4>
                                            </div>
                                            <div className="fuct">
                                                <h2>100+</h2>
                                                <h4>Clients</h4>
                                            </div>
                                        </div>
                                    </article>
                                </div>  
                            </div>     
                        </div>

                    </div>
                    <div className="col-md-6 col-sm-12 col-xs-12">
                        <div className="pad-l-45 pad-r-45 block">
                            <div className="row">
                                {services.map((service)=>(
                                    <div className="col-lg-12 col-xxl-6" key={service._id}>
                                    <div className="block-feature">
                                        <i className={service.classIcon}></i>
                                        <a href="/">{service.title}</a>
                                        <p>
                                        {service.text}
                                        </p>
                                    </div>
                                </div>
                                ))}
                            </div>
                        </div>
                    </div>

                </div>
            </div>

        </section>  
        </>
    )
}

export default Service1
